import { useState } from "react"
import Button from "./Button";
import "../styles/Item.scss"

function Item( { onClick, id, change, name, amount, measure, active, onVisChange, onAmountChange}){
    const [itemAmount, setItemAmount] = useState(amount);
    const [tempChange, setTempChange] = useState(amount)

    const changeAmount = (event) =>{
        const tempAmount = event.target.value;
        setItemAmount(tempAmount);
    }

    const handleAmountChange = () => {
        if (change){
            setTempChange(itemAmount)
        }
        onAmountChange(id);
    }

    const handleDiscardChange = () =>{
        setItemAmount(tempChange)
       onAmountChange(id)
    }

    let style;
    if (active){
    style="item"
    }else{
    style="item item-inactive"   
    }


    
    return(
        <div className={style}>
                        <span className="item__btn-right">
            {!active ? <Button onClick={()=>onClick(id)} text="ðŸ—‘"/>:<Button onClick={()=>handleAmountChange()} text={change ? "âœŽ": "âœ"}/>}
            </span>
            {!active? <Button onClick={()=>onVisChange(id)} text="âœ˜" active={true}/> : <Button onClick={()=>onVisChange(id)} text="âœ”" active={false}/> }
            {change && active ? <span><input className="item__input" type="number" value={itemAmount} min="1" max="100" onChange={changeAmount}></input>{measure}<span className="item__btn-right"><Button  onClick={()=>{handleDiscardChange()}} text="â†©"/></span></span> : <span className="item__info">{name} {itemAmount} {measure}</span>} 

        </div>
    )
}

export default Item