import { useEffect, useState } from "react";
import ItemsList from "./components/ItemsList";
import Selector from "./components/Selector";
import Button from "./components/Button";
import shopDataJson from "./shopData";
import historyDataJson from "./historyData";
import "./styles/App.scss"

function App() {
  const [shoptDb, setShopDb] = useState(shopDataJson)
  const [historyDb, setHistoryDb] = useState(historyDataJson)
  const [visability, setVisability] = useState(false)


  useEffect(() =>{
  if(localStorage.getItem("shopListJson")===null){
    localStorage.setItem("shopListJson", shopDataJson)
    setShopDb(localStorage.getItem("shopListJson"))
  }else{
    setShopDb(localStorage.getItem("shopListJson"))
  }

  if(localStorage.getItem("shopHistoryJson")===null){
    localStorage.setItem("shopHistoryJson", historyDataJson)
    setHistoryDb(localStorage.getItem("shopHistoryJson"))
  }else{
    setHistoryDb(localStorage.getItem("shopHistoryJson"))
  }

}, [])

const add = () =>
{
    const db = JSON.parse(localStorage.getItem("shopListJson"));
    const name = document.getElementById("productName");
    const amount = document.getElementById("productAmount");
    const measure = document.getElementById("productMeasure");
    const item = {
        name: name.value,
        amount: amount.value,
        measure: measure.value,
        active: true,
        change: false,
    }
    db.push(item);
    localStorage.setItem("shopListJson", JSON.stringify(db));
    setShopDb(localStorage.getItem("shopListJson"));
    changeHistory(name.value, measure.value);
}

const changeHistory = (name, measure) =>{
  const db = JSON.parse(localStorage.getItem("shopHistoryJson"))
  const newItem = {
    name:name,
    tick: 1,
    measure: measure
  }
  let inList = false
  db.map((item) => {
    if (item.name===name){
      if (item.tick>=1){
     item.tick++
     inList = true
    }}
  })
  if(!inList){
    db.push(newItem)
  }
  localStorage.setItem("shopHistoryJson", JSON.stringify(db))
}

const deleteItem = (id) =>{
  const tempDb = JSON.parse(shoptDb)
  tempDb.splice(id, 1)
  localStorage.setItem("shopListJson", JSON.stringify(tempDb))
  setShopDb(localStorage.getItem("shopListJson"));
}

const handleVisibility = (id) =>{
  const db = JSON.parse(shoptDb);
  db[id].active = !db[id].active;
  localStorage.setItem("shopListJson", JSON.stringify(db));
  setShopDb(localStorage.getItem("shopListJson"))
}

const handleChange = (id)=>{
  const db = JSON.parse(shoptDb);
  db[id].change = !db[id].change;
  localStorage.setItem("shopListJson", JSON.stringify(db));
  setShopDb(localStorage.getItem("shopListJson"))
}


  return (
    <div className="App">
      <Button onClick={()=>setTimeout(setVisability(!visability), 200)} text="âŒ¨" active={visability}/>
      {visability ? <Selector historyDb={historyDb} onClick={add}/> : ""}
      <ItemsList shopListDb={shoptDb} onClick={deleteItem} onVisChange={handleVisibility} onAmountChange={handleChange}/>
    </div>
  );
}

export default App;
