import "../styles/history.scss"

function History({ onClick, history }) {

    const tempHis = JSON.parse(history);
    const list = tempHis.map((item, index) => {
    if (index <=9 )
        return(<button className="history__btn" id={index} name={item.name} measure={item.measure} onClick={()=>onClick(item.name, item.measure)}>{item.name}({item.measure})</button>)
    })
    return (
        <div className="history">
            <h3>NajczÄ™Å›ciej dodawane:</h3>
            {list}
        </div>
    )
}

export default History