
import Item from "./Item";
import "../styles/ItemList.scss"

function ItemsList({ onClick, shopListDb, onVisChange, onAmountChange }){
    const listJson = JSON.parse(shopListDb);
    const list = listJson.map((item, index) => <div>
            <Item id={index} name={item.name} amount={item.amount} measure={item.measure} active={item.active} change={item.change} onClick={onClick} onVisChange={onVisChange} onAmountChange={onAmountChange}/>
    </div>)
    


    return(
        <div className="itemList">
            {list}
        </div>
    )
}

export default ItemsList