import { useState } from "react"
import History from "./History"
import Button from "./Button"

import "../styles/Selector.scss"

function Selector({ onClick, historyDb }) {

    const [name, setName] = useState("")
    const[amount, setAmount] = useState(0)
    const [measure, setMeasure] = useState("")
    const [history, setHistory] = useState(historyDb);
    const [visability, setVisability] = useState(false);

    const handleNameChange = (event) =>{
        setName(event.target.value)
    }
    const handleAmountChange = (event) =>{
        setAmount(event.target.value)
    }
    const handleMeasureChange = (event) =>{
        setMeasure(event.target.value)
    }

    const handleHistory = (historyName, historyMeasure) =>{
        setName(historyName)
        setMeasure(historyMeasure)
        setVisability(false)
    }


    const selectFromHistory = () => {
        setVisability(!visability)
        const tempHistArr = JSON.parse(localStorage.getItem("shopHistoryJson"));

        function compare( a, b ) {
            if ( a.tick < b.tick ){
              return 1;
            }
            if ( a.tick > b.tick ){
              return -1;
            }
            return 0;
          }
          
          tempHistArr.sort( compare );

          setHistory(JSON.stringify(tempHistArr));
    }


    return (
        <div className="selector">
            <Button onClick={()=>selectFromHistory()} text="â±" active={visability}/>
            <div className="selector__wrapper">
            <div className="selector__box">
            <input className="selector__input" id="productName" type="text" onChange={handleNameChange} value={name}></input>
            <div className="selector__box-underline">
            <input className="selector__input" id="productAmount" type="number" onChange={handleAmountChange} min="0" value={amount}></input>
            <select className="selector__select" id="productMeasure" name="type"onChange={handleMeasureChange} value={measure}>
                <option value="g">g</option>
                <option value="dag">dag</option>
                <option value="kg">kg</option>
                <option value="ml">ml</option>
                <option value="l">l</option>
                <option value="sz">sz</option>
            </select>
            <span style={{float: "right"}}>
            { name!=="" && amount!=="0" && amount!==0 ? <Button onClick={onClick} text="+" /> : ""}
            </span>
            </div>
            {visability ? <History history={history} onClick={handleHistory} />: ""}
            </div>
            </div>
        </div>
    )
}

export default Selector